<template>
  <div class="customer-service">
    <!-- 移除聊天图标，只保留聊天窗口 -->
    <div v-show="showChat" class="chat-window">
      <div class="chat-header">
        <div class="header-content">
          <i class="fas fa-headset header-icon"></i>
          <span class="header-title">客户支持</span>
        </div>
        <button class="close-btn" @click="closeChat" title="关闭">
          <i class="fas fa-times"></i>
        </button>
      </div>

      <div class="chat-messages" ref="messageContainer">
        <div v-for="msg in messages" :key="msg._id" 
          :class="['message', getMessageClass(msg)]">
          <div class="sender-info" v-if="showSenderInfo(msg)">
            {{ getSenderLabel(msg) }}
          </div>
          <div class="message-content">
            <template v-if="msg.type === 'image'">
              <img :src="msg.content" alt="聊天图片" @click="previewImage = msg.content">
            </template>
            <template v-else>
              {{ msg.content }}
            </template>
          </div>
          <div class="message-time">{{ formatTime(msg.createdAt) }}</div>
        </div>
      </div>

      <div class="chat-input">
        <!-- 添加表情择器 -->
        <div class="emoji-picker" v-if="showEmojiPicker">
          <div class="emoji-container">
            <span v-for="emoji in emojis" 
              :key="emoji"
              @click="insertEmoji(emoji)"
              class="emoji-item"
            >{{ emoji }}</span>
          </div>
        </div>

        <!-- 添加工具栏 -->
        <div class="input-toolbar">
          <button class="tool-btn" @click="toggleEmojiPicker">
            <i class="far fa-smile"></i>
          </button>
          <label class="tool-btn">
            <i class="far fa-image"></i>
            <input 
              type="file"
              accept="image/*"
              @change="handleImageUpload"
              style="display: none"
            >
          </label>
        </div>

        <input 
          v-model="newMessage" 
          @keyup.enter="sendMessage"
          placeholder="输入消息..."
          ref="messageInput"
        >
        <button 
          @click.stop="sendMessage()"
          :disabled="!canSendMessage"
        >
          <i class="fas fa-paper-plane"></i>
        </button>
      </div>
    </div>

    <!-- 添加图片预览弹窗 -->
    <div v-if="previewImage" class="image-preview-modal" @click="closePreview">
      <div class="preview-content" @click.stop>
        <img :src="previewImage" alt="预览图片">
        <div class="preview-actions">
          <button class="cancel-btn" @click="closePreview">取消</button>
          <button class="send-btn" @click="sendImage">发送</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CustomerService',
  props: {
    orderId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      showChat: true,
      messages: [],
      newMessage: '',
      unreadCount: 0,
      socketInitialized: false,
      isLoading: false,
      localMessages: [],
      processedMessageIds: new Set(),
      orderInfo: null,
      notificationSound: null,
      showEmojiPicker: false,
      emojis: ['😊', '😂', '🤔', '👍', '❤️', '😭', '🎉', '🌟', '💪', '🙏'],
      previewImage: null,
      imageFile: null,
      uploadingImage: false
    }
  },
  computed: {
    canSendMessage() {
      return !this.uploadingImage && (this.newMessage.trim() || this.imageFile);
    }
  },
  methods: {
    closeChat() {
      this.$emit('chat-closed');
    },
    initSocket() {
      if (this.socketInitialized) return;

      if (!this.$socket) {
        console.error('Socket not initialized');
        return;
      }

      // 加入订单聊天室
      this.$socket.emit('join-chat', { 
        room: `order:${this.orderId}`,
        userId: localStorage.getItem('currentUserId')
      });

      // 监听新消息
      this.$socket.on('newMessage', (data) => {
        
        if (!this.processedMessageIds.has(data.message._id)) {
          this.processedMessageIds.add(data.message._id);
          this.messages.push(data.message);
          this.$nextTick(() => {
            this.scrollToBottom();
          });
        }
      });

      this.socketInitialized = true;
    },
    async loadMessages() {
      if (this.isLoading) return;
      
      try {
        this.isLoading = true;
        
        const response = await this.$api.get(`/api/messages/${this.orderId}`);
        this.messages = response.data;
        
        this.$nextTick(() => {
          this.scrollToBottom();
        });
      } catch (error) {
        console.error('Load messages error:', error);
      } finally {
        this.isLoading = false;
      }
    },
    async sendMessage(imageUrl = null) {
      if ((!this.newMessage.trim() && !imageUrl) || !this.orderId) return;
      
      try {
        const messageData = {
          content: imageUrl || this.newMessage.trim(),
          type: imageUrl ? 'image' : 'text',
          sender: 'merchant'
        };

        
        const response = await this.$api.post(`/api/messages/${this.orderId}`, messageData);
        
        if (response.data) {
          if (!imageUrl) {
            this.newMessage = '';
          }
          
          if (!this.processedMessageIds.has(response.data._id)) {
            this.processedMessageIds.add(response.data._id);
            this.messages.push(response.data);
          }
          
          this.$nextTick(() => {
            this.scrollToBottom();
          });
        }
      } catch (error) {
        console.error('Send message error:', error);
      }
    },
    formatTime(date) {
      return new Date(date).toLocaleTimeString();
    },
    scrollToBottom() {
      if (this.$refs.messageContainer) {
        this.$refs.messageContainer.scrollTop = this.$refs.messageContainer.scrollHeight;
      }
    },
    getMessageClass(message) {
      if (this.isMerchant()) {
        return message.sender === 'merchant' ? 'sent' : 'received';
      }
      return message.sender === 'user' ? 'sent' : 'received';
    },
    showSenderInfo(message) {
      return message.sender === 'merchant' || message.sender === 'user';
    },
    getSenderLabel(message) {
      if (this.isMerchant()) {
        return message.sender === 'merchant' ? '商家' : '客户';
      }
      return message.sender === 'user' ? '我' : '商家';
    },
    async fetchOrderInfo() {
      try {
        const response = await this.$api.get(`/api/orders/${this.orderId}`);
        this.orderInfo = response.data;
      } catch (error) {
        console.error('获取订单信息失败:', error);
      }
    },
    isMerchant() {
      const currentUserId = localStorage.getItem('currentUserId');
      const userRole = localStorage.getItem('userRole');
      
      return userRole === 'superadmin' || 
             (this.orderInfo && this.orderInfo.userId === currentUserId);
    },
    playNotificationSound() {
      if (!this.notificationSound) {
        this.notificationSound = new Audio('/notification.mp3');
      }
      this.notificationSound.play().catch(error => {
        console.warn('Failed to play notification sound:', error);
      });
    },
    showSystemNotification(messageContent, orderId) {
      if (!("Notification" in window) || Notification.permission !== "granted") {
        return;
      }

      // 创建系统通知
      const notification = new Notification("新消息提醒", {
        body: messageContent,
        icon: "/favicon.ico",
        tag: `chat-${orderId}`,
        requireInteraction: true,
        silent: true // 禁用通知的默认声音
      });

      notification.onclick = () => {
        window.focus();
        this.showChat = true;
        
        // 跳转到订单查询页面并显示对应订单
        this.$router.push({
          path: '/order-query',
          query: { orderId: orderId }
        });
        
        notification.close();
      };

      // 10秒后自动关闭通知
      setTimeout(() => {
        notification.close();
      }, 10000);
    },
    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    },
    insertEmoji(emoji) {
      this.newMessage += emoji;
      this.$refs.messageInput.focus();
    },
    async handleImageUpload(event) {
      const file = event.target.files[0];
      if (!file) return;
      
      // 验证文件类型和大小
      if (!file.type.startsWith('image/')) {
        this.$message.error('请选择图片文件');
        return;
      }
      
      if (file.size > 5 * 1024 * 1024) { // 5MB限制
        this.$message.error('图片大小不能超过5MB');
        return;
      }
      
      try {
        // 创建 FormData
        const formData = new FormData();
        formData.append('image', file);
        
        // 上传图片
        const response = await this.$api.post('/api/messages/upload-image', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        // 发送图片消息
        if (response.data.imageUrl) {
          await this.sendMessage(response.data.imageUrl);
        }
      } catch (error) {
        console.error('Upload image error:', error);
        this.$message.error('图片上传失败');
      }
    },
    closePreview() {
      if (this.previewImage) {
        URL.revokeObjectURL(this.previewImage);
      }
      this.previewImage = null;
      this.imageFile = null;
    },
    async sendImage() {
      if (!this.imageFile || this.uploadingImage) return;
      
      try {
        this.uploadingImage = true;
        
        // 创建 FormData
        const formData = new FormData();
        formData.append('image', this.imageFile);
        formData.append('orderId', this.orderId);
        
        // 上传图片
        const response = await this.$api.post('/api/messages/upload-image', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        // 发送图片消息
        await this.sendMessage(response.data.imageUrl);
        
        this.closePreview();
      } catch (error) {
        console.error('Upload image error:', error);
        this.$message.error('图片上传失败');
      } finally {
        this.uploadingImage = false;
      }
    },
    reopenChat() {
      this.showChat = true;
      this.unreadCount = 0; // 清除未读计数
      
      // 滚动到最新消息
      this.$nextTick(() => {
        this.scrollToBottom();
      });
    },
    // 添加新的通知发送方法
    sendNotification(data) {
      // 发送全局通知
      this.$root.$emit('new-chat-notification', {
        orderId: data.orderId,
        message: data.message.content,
        displayId: data.orderId.slice(-8)
      });

      // 显示系统通知
      this.showSystemNotification(data.message.content, data.orderId);
    }
  },
  watch: {
    localMessages: {
      deep: true,
      handler() {
        this.$nextTick(() => {
          this.scrollToBottom();
        });
      }
    },
    orderId: {
      immediate: true,
      handler(newOrderId, oldOrderId) {
        if (newOrderId !== oldOrderId) {
          
          // 清理旧的socket连接
          if (this.$socket && oldOrderId) {
            this.$socket.off('newMessage');
            this.$socket.emit('leave-chat', { 
              room: `order:${oldOrderId}`,
              userId: localStorage.getItem('currentUserId')
            });
          }
          
          // 重置状态
          this.messages = [];
          this.processedMessageIds.clear();
          this.socketInitialized = false;
          
          // 初始化新的连接
          if (newOrderId) {
            this.initSocket();
            this.loadMessages();
          }
        }
      }
    },
    '$route.query.orderId': {
      handler(newOrderId) {
        if (newOrderId === this.orderId) {
          // 不重置任何状态
        }
      },
      immediate: true
    }
  },
  async mounted() {
    if (this.orderId) {
      this.initSocket();
      await this.loadMessages();
    }
  },
  beforeDestroy() {
    if (this.$socket) {
      this.$socket.off('newMessage');
      this.$socket.emit('leave-chat', { 
        room: `order:${this.orderId}`,
        userId: localStorage.getItem('currentUserId')
      });
    }
  }
}
</script>

