<template>
  <section class="table-section">
    <div v-if="loading" class="loading-overlay">
      <div class="spinner"></div>
    </div>
    
    <div v-if="error" class="error-message">
      {{ error }}
    </div>

    <div class="table-header">
      <div class="header-left">
        <h2>通道列表</h2>
        <div class="header-actions">
          <div class="view-toggle">
            <button 
              class="view-toggle-btn" 
              @click="toggleViewMode"
              :class="{ 'active': viewMode === 'card' }"
              title="卡片视图"
            >
              <i class="fas fa-th"></i>
            </button>
            <button 
              class="view-toggle-btn" 
              @click="toggleViewMode"
              :class="{ 'active': viewMode === 'table' }"
              title="表格视图"
            >
              <i class="fas fa-table"></i>
            </button>
          </div>
        </div>
      </div>
      <div class="header-right">
        <div class="search-container">
          <i class="fas fa-search search-icon"></i>
          <input 
            type="text" 
            v-model="searchQuery" 
            placeholder="搜索通道..." 
            aria-label="搜索通道"
            class="search-input"
          >
          <button 
            v-if="searchQuery" 
            class="clear-search-btn"
            @click="searchQuery = ''"
            title="清除搜索"
          >
            <i class="fas fa-times"></i>
          </button>
        </div>
      </div>
    </div>

    <!-- 卡片视图 -->
    <div v-if="viewMode === 'card'" class="channel-cards">
      <div 
        v-for="channel in filteredChannels" 
        :key="channel.id" 
        class="channel-card"
        :class="{ 
          'enabled': channel.status === '启用',
          'disabled': channel.status === '未启用'
        }"
      >
        <div class="card-header">
          <div class="card-title-area">
            <h3 class="card-title">{{ channel.name }}</h3>
            <div class="channel-type-icons">
              <!-- 对于支付宝/微信类型，并排显示两个图标 -->
              <template v-if="channel.type === '支付宝/微信'">
                <span class="wechat-icon card-icon" title="微信"></span>
                <span class="alipay-icon card-icon" title="支付宝"></span>
              </template>
              <!-- 对于支付宝类型使用自定义图标 -->
              <span v-else-if="channel.type === '支付宝'" class="alipay-icon card-icon" title="支付宝"></span>
              <!-- 对于微信类型使用自定义图标 -->
              <span v-else-if="channel.type === '微信'" class="wechat-icon card-icon" title="微信"></span>
              <!-- 其他支付方式显示文字徽章 -->
              <span v-else class="channel-type-badge text-badge">{{ channel.type }}</span>
            </div>
          </div>
          <span 
            class="channel-status"
            :class="{
              'status-enabled': channel.status === '启用',
              'status-disabled': channel.status === '未启用'
            }"
            @click="toggleStatus(channel)"
          >
            <span class="status-dot"></span>
            {{ channel.status }}
          </span>
        </div>
        
        <div class="card-body">
          <div class="info-grid">
            <div class="info-item">
              <div class="info-icon"><i class="fas fa-fingerprint"></i></div>
              <div class="info-content">
                <span class="info-label">通道标识</span>
                <span class="info-value">{{ channel.identifier }}</span>
              </div>
            </div>
            <div class="info-item">
              <div class="info-icon"><i class="fas fa-calendar-alt"></i></div>
              <div class="info-content">
                <span class="info-label">添加时间</span>
                <span class="info-value">{{ channel.addTime }}</span>
              </div>
            </div>
            <div class="info-item">
              <div class="info-icon"><i class="fas fa-users"></i></div>
              <div class="info-content">
                <span class="info-label">在线人数</span>
                <span class="info-value">{{ channel.onlineUsers }}</span>
              </div>
            </div>
            <div class="info-item">
              <div class="info-icon"><i class="fas fa-money-bill-wave"></i></div>
              <div class="info-content">
                <span class="info-label">累计金额</span>
                <span class="info-value">¥{{ channel.totalAmount }}</span>
              </div>
            </div>
          </div>
        </div>
        
        <div class="card-actions">
          <button class="btn-qr" @click="generateQR(channel)">
            <i class="fas fa-qrcode"></i>
            <span>生成二维码</span>
          </button>
          <button class="btn-settings" @click="openSettings(channel)">
            <i class="fas fa-cog"></i>
            <span>参数设置</span>
          </button>
        </div>
      </div>
    </div>
    
    <!-- 桌面表格视图 -->
    <div v-else class="table-wrapper">
      <div class="table-container">
        <table id="channelTable" role="table" aria-label="通道列表">
          <thead>
            <tr>
              <th v-for="header in tableHeaders" :key="header" 
                  :class="{ 'action-header': header === '操作', 'status-header': header === '状态' }">
                <div class="th-content">
                  <!-- 给每个表头添加对应的图标 -->
                  <i :class="getHeaderIcon(header)" class="header-icon"></i>
                  <span class="header-text">{{ header }}</span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="channel in filteredChannels" :key="channel.id" 
                :class="{ 'enabled-row': channel.status === '启用', 'disabled-row': channel.status === '未启用' }">
              <td class="id-cell">{{ channel.id }}</td>
              <td>
                <div class="channel-type-cell">
                  <!-- 对于支付宝/微信类型，并排显示两个图标 -->
                  <template v-if="channel.type === '支付宝/微信'">
                    <span class="wechat-icon table-icon" title="微信"></span>
                    <span class="alipay-icon table-icon" title="支付宝"></span>
                  </template>
                  <!-- 对于支付宝类型使用自定义图标 -->
                  <span v-else-if="channel.type === '支付宝'" class="alipay-icon table-icon" title="支付宝"></span>
                  <!-- 对于微信类型使用自定义图标 -->
                  <span v-else-if="channel.type === '微信'" class="wechat-icon table-icon" title="微信"></span>
                  <!-- 其他支付方式显示文字 -->
                  <span v-else class="type-text">{{ channel.type }}</span>
                </div>
              </td>
              <td>
                <div class="channel-name">{{ channel.name }}</div>
              </td>
              <td class="identifier-cell">
                <div class="identifier-wrapper">
                  <i class="fas fa-fingerprint identifier-icon"></i>
                  <span>{{ channel.identifier }}</span>
                </div>
              </td>
              <td>
                <div class="date-wrapper">
                  <i class="fas fa-calendar-alt date-icon"></i>
                  <span>{{ channel.addTime }}</span>
                </div>
              </td>
              <td class="numeric-cell">
                <div class="users-wrapper">
                  <i class="fas fa-users users-icon"></i>
                  <span>{{ channel.onlineUsers }}</span>
                </div>
              </td>
              <td class="numeric-cell amount-cell">
                <div class="amount-wrapper">
                  <i class="fas fa-money-bill-wave amount-icon"></i>
                  <span>¥{{ channel.totalAmount }}</span>
                </div>
              </td>
              <td class="actions-cell">
                <div class="actions-wrapper">
                  <button class="btn-action btn-qr" @click="generateQR(channel)" title="生成二维码">
                    <i class="fas fa-qrcode"></i>
                    <span class="btn-text">生成二维码</span>
                  </button>
                  <button class="btn-action btn-settings" @click="openSettings(channel)" title="参数设置">
                    <i class="fas fa-cog"></i>
                    <span class="btn-text">参数设置</span>
                  </button>
                </div>
              </td>
              <td class="status-cell">
                <span 
                  class="channel-status"
                  :class="{
                    'status-enabled': channel.status === '启用',
                    'status-disabled': channel.status === '未启用'
                  }"
                  @click="toggleStatus(channel)"
                  role="button"
                  tabindex="0"
                  :title="'点击' + (channel.status === '启用' ? '禁用' : '启用') + '通道'"
                >
                  <span class="status-dot"></span>
                  {{ channel.status }}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
      <!-- 无数据状态 -->
      <div v-if="filteredChannels.length === 0" class="no-data-state">
        <div class="no-data-icon">
          <i class="fas fa-search"></i>
        </div>
        <p class="no-data-text">没有找到匹配的通道</p>
        <button v-if="searchQuery" class="clear-search" @click="searchQuery = ''">
          清除搜索
        </button>
      </div>
    </div>

    <!-- 修改模态框部分的代码 -->
    <div id="modal" class="modal" v-if="showModal">
      <div class="modal-content" :class="{ 'qr-modal': modalType === 'qr', 'settings-modal': modalType === 'settings' }">
        <span class="close-button" @click="closeModal">&times;</span>
        <div v-if="modalType === 'qr'" class="qr-content">
          <h3>通道支付二维码</h3>
          <div class="domain-selector" v-if="domains.length > 0">
            <label for="domain-select">选择域名:</label>
            <el-select v-model="selectedDomain" id="domain-select" placeholder="请选择" @change="generateQR(currentChannel)">
              <el-option
                v-for="domain in domains"
                :key="domain._id"
                :label="domain.name"
                :value="domain.name">
              </el-option>
            </el-select>
          </div>
          <div class="qr-image-container">
            <img v-if="qrCodeUrl" :src="qrCodeUrl" alt="二维码图片">
          </div>
          <div v-if="qrCodeUrl" class="qr-actions">
            <button class="btn-save-qr" @click="saveQRCode">
              <i class="fas fa-download"></i> 保存二维码
            </button>
          </div>
          <p v-else>{{ modalMessage }}</p>
        </div>
        <div v-else-if="modalType === 'settings'" class="settings-container">
          <div class="settings-header">
            <div class="header-icon">
              <i class="fas fa-sliders-h"></i>
            </div>
            <div class="header-text">
              <h3>参数设置</h3>
              <p>通道: {{ currentChannel.name }} (ID: {{ currentChannel.id }})</p>
            </div>
          </div>
          
          <div class="settings-body">
            <div class="settings-tabs">
              <button
                class="tab-btn"
                :class="{ active: activeTab === 'basic' }"
                @click="activeTab = 'basic'">
                <i class="fas fa-cogs"></i> 基本设置
              </button>
              <button
                class="tab-btn"
                :class="{ active: activeTab === 'payment' }"
                @click="activeTab = 'payment'">
                <i class="fas fa-credit-card"></i> 支付参数
              </button>
              <button
                class="tab-btn"
                :class="{ active: activeTab === 'features' }"
                @click="activeTab = 'features'">
                <i class="fas fa-toggle-on"></i> 功能开关
              </button>
              <button
                class="tab-btn"
                :class="{ active: activeTab === 'security' }"
                @click="activeTab = 'security'">
                <i class="fas fa-shield-alt"></i> 安全设置
              </button>
            </div>

            <form @submit.prevent="saveSettings" class="settings-form">
              <!-- 基本设置 -->
              <div v-show="activeTab === 'basic'" class="tab-content">
                <div class="form-section">
                  <h4 class="section-title">通道状态</h4>
              <div class="form-group">
                <label for="status">通道状态</label>
                    <div class="custom-select">
                <select id="status" v-model="currentChannel.status">
                  <option value="启用">启用</option>
                  <option value="未启用">未启用</option>
                </select>
                      <i class="fas fa-chevron-down select-arrow"></i>
                    </div>
                  </div>
                </div>
                <div class="form-section">
                  <h4 class="section-title">公告设置</h4>
                   <div class="form-group switch-group">
                      <label for="announcement-enabled">启用公告</label>
                      <el-switch id="announcement-enabled" v-model="currentChannel.announcement.enabled"></el-switch>
                   </div>
                  <transition name="slide-fade">
                    <div v-if="currentChannel.announcement.enabled" class="form-group">
                      <label for="announcement">公告内容</label>
                      <textarea
                        id="announcement"
                        v-model="currentChannel.announcement.content"
                        placeholder="请输入公告内容，支持多行文本"
                        rows="4"
                      ></textarea>
                    </div>
                  </transition>
                </div>
              </div>

              <!-- 支付参数 -->
              <div v-show="activeTab === 'payment'" class="tab-content">
                <div class="form-section">
                   <h4 class="section-title">支付参数 ({{ currentChannel.identifier }})</h4>
                <div class="form-section">
                  <h4 class="section-title">金额限制</h4>
                  <div class="form-group">
                    <label for="minAmount">最小支付金额</label>
                    <input type="number" id="minAmount" v-model="currentChannel.minAmount" placeholder="请输入最小金额，0表示不限制">
                  </div>
                  <div class="form-group">
                    <label for="maxAmount">最大支付金额</label>
                    <input type="number" id="maxAmount" v-model="currentChannel.maxAmount" placeholder="请输入最大金额，0表示不限制">
                  </div>
                </div>
                <template v-if="currentChannel.identifier === 'lilipay'">
                  <div class="form-group">
                    <label for="merchantId">商户ID</label>
                        <input type="text" id="merchantId" v-model="currentChannel.customParams.merchantId" placeholder="请输入商户ID">
                  </div>
                  <div class="form-group">
                    <label for="key">商户密钥</label>
                        <input type="password" id="key" v-model="currentChannel.customParams.key" placeholder="请输入商户密钥">
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'f2f'">
                  <div class="form-group">
                    <label for="userId">商户ID</label>
                        <input type="text" id="userId" v-model="currentChannel.customParams.userId" placeholder="请输入商户ID">
                  </div>
                  <div class="form-group">
                    <label for="directJumpUrl">直接跳转链接</label>
                        <input type="text" id="directJumpUrl" v-model="currentChannel.customParams.directJumpUrl" placeholder="请输入直接跳转链接(可选)">
                  </div>
                  <div class="form-group switch-group">
                    <label for="enableDirectJump">启用直接跳转</label>
                    <el-switch id="enableDirectJump" v-model="currentChannel.customParams.enableDirectJump"></el-switch>
                    <div class="info-box" v-if="currentChannel.customParams.enableDirectJump"><p>启用后，用户扫码将直接跳转到设置的链接，不显示支付页面</p></div>
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'ysm'">
                  <div class="form-group">
                    <label for="appid">appid</label>
                        <input type="text" id="appid" v-model="currentChannel.customParams.appid" placeholder="请输入appid">
                  </div>
                  <div class="form-group">
                    <label for="appSecret">appSecret</label>
                        <input type="password" id="appSecret" v-model="currentChannel.customParams.appSecret" placeholder="请输入appSecret">
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'ZFBZZ'">
                  <div class="form-group">
                    <label for="userId">支付宝用户ID</label>
                            <input type="text" id="userId" v-model="currentChannel.customParams.userId" placeholder="请输入支付宝用户ID (chatUserId)">
                  </div>
                  <div class="form-group">
                    <label for="displayName">显示名称</label>
                            <input type="text" id="displayName" v-model="currentChannel.customParams.displayName" placeholder="请输入显示名称">
                  </div>
                  <div class="form-group">
                    <label for="chatUserName">用户名称</label>
                            <input type="text" id="chatUserName" v-model="currentChannel.customParams.chatUserName" placeholder="请输入用户名称">
                  </div>
                  <div class="form-group">
                    <label for="chatLoginId">登录ID</label>
                            <input type="text" id="chatLoginId" v-model="currentChannel.customParams.chatLoginId" placeholder="请输入登录ID (如156******05)">
                  </div>
                  <div class="form-group">
                    <label for="directJumpUrl">直接跳转链接</label>
                            <input type="text" id="directJumpUrl" v-model="currentChannel.customParams.directJumpUrl" placeholder="请输入直接跳转链接(可选)">
                  </div>
                  <div class="form-group switch-group">
                    <label for="enableDirectJump">启用直接跳转</label>
                    <el-switch id="enableDirectJump" v-model="currentChannel.customParams.enableDirectJump"></el-switch>
                    <div class="info-box" v-if="currentChannel.customParams.enableDirectJump"><p>启用后，用户扫码将直接跳转到设置的链接，不显示支付页面</p></div>
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'golden'">
                  <div class="form-group">
                    <label for="userId">支付宝账号UID</label>
                            <input type="text" id="userId" v-model="currentChannel.customParams.userId" placeholder="请输入支付宝账号UID">
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'wxtoqq'">
                  <div class="form-group">
                    <label for="userId">商户ID</label>
                            <input type="text" id="userId" v-model="currentChannel.customParams.userId" placeholder="请输入商户ID">
                  </div>
                  <div class="form-group">
                    <label for="qqNumber">QQ号码</label>
                            <input type="text" id="qqNumber" v-model="currentChannel.customParams.qqNumber" placeholder="请输入QQ号码">
                  </div>
                  <div class="form-group">
                    <label for="realName">真实姓名</label>
                            <input type="text" id="realName" v-model="currentChannel.customParams.realName" placeholder="请输入真实姓名">
                  </div>
                </template>
                <template v-else-if="currentChannel.identifier === 'hkrt'">
                  <div class="form-group">
                    <label for="token">Token</label>
                            <input type="text" id="token" v-model="currentChannel.customParams.token" placeholder="请输入mini_token值">
                  </div>
                  <div class="form-group">
                    <label for="cookie">Cookie (可选)</label>
                            <input type="text" id="cookie" v-model="currentChannel.customParams.cookie" placeholder="请输入Cookie值">
                  </div>
                </template>
                <template v-else>
                      <div class="form-group">
                          <label for="customUserId">{{ isPaymentChannel(currentChannel.name) ? '支付参数' : '用户ID' }}</label>
                          <input type="text" id="customUserId" v-model="currentChannel.customParams.userId" :placeholder="isPaymentChannel(currentChannel.name) ? '请输入支付参数' : '请输入用户ID'">
                      </div>
                </template>
              </div>
                </div>
                
              <!-- 功能开关 -->
              <div v-show="activeTab === 'features'" class="tab-content">
                <div class="form-section">
                  <h4 class="section-title">功能开关</h4>
                  <div class="form-group switch-group">
                    <label for="wechat-guide">微信浏览器引导</label>
                    <el-switch id="wechat-guide" v-if="currentChannel.identifier === 'f2f'" v-model="currentChannel.customParams.wechatGuideEnabled"></el-switch>
                     <div class="info-box" v-if="currentChannel.identifier === 'f2f'"><p>启用后，用户在微信内打开支付页面时会显示引导提示，引导用户使用浏览器打开</p></div>
                  </div>
                   <div class="form-group switch-group">
                    <label for="chat-feature">启用聊天功能</label>
                    <el-switch id="chat-feature" v-model="currentChannel.chatFeature.enabled"></el-switch>
                     <div class="info-box"><p>启用后，用户可以在支付页面使用聊天功能进行交流</p></div>
              </div>
                  <div class="form-group switch-group">
                    <label for="name-required">需要用户姓名</label>
                    <el-switch id="name-required" v-model="currentChannel.nameRequired.enabled"></el-switch>
                     <div class="info-box"><p>启用后，用户在支付时需要输入真实姓名</p></div>
                </div>
                    </div>
              </div>

              <!-- 安全设置 -->
              <div v-show="activeTab === 'security'" class="tab-content">
                 <div class="form-section">
                  <h4 class="section-title">安全设置</h4>
                  <div class="form-group switch-group">
                    <label for="random-decimal">随机小数点</label>
                    <template v-if="['ZFBZZ', 'golden', 'wxtoqq', 'hkrt'].includes(currentChannel.identifier)">
                        <el-switch id="random-decimal" v-model="currentChannel.customParams.randomDecimal"></el-switch>
                        <div class="info-box" v-if="currentChannel.customParams.randomDecimal">
                            <p v-if="['ZFBZZ', 'golden'].includes(currentChannel.identifier)">启用后，支付金额将随机减少0.01-0.09元</p>
                            <p v-else>启用后，支付金额将随机减少0.01-0.99元</p>
                  </div>
                    </template>
                    <p v-else class="text-muted">当前通道不支持此功能</p>
                </div>
                   <div class="form-group switch-group">
                    <label for="share-blacklist">共享黑名单</label>
                    <el-switch id="share-blacklist" v-model="currentChannel.shareBlacklist.enabled" @change="handleShareBlacklistChange"></el-switch>
                    <div class="info-box" v-if="currentChannel.shareBlacklist.enabled"><p>启用后，其他用户拉黑的IP将无法扫描您的支付码，提高安全性</p></div>
                    </div>
                  </div>
                <div class="form-section">
                  <h4 class="section-title">地区禁用</h4>
                   <div class="form-group switch-group">
                    <label for="region-blacklist">启用地区禁用</label>
                    <el-switch id="region-blacklist" v-model="currentChannel.regionBlacklist.enabled"></el-switch>
              </div>
                <div v-if="currentChannel.regionBlacklist.enabled" class="region-settings">
                    <el-button type="primary" icon="el-icon-plus" @click="addRule" class="add-rule-btn">添加地区规则</el-button>
                  <div v-if="hasBlockedRegions" class="blocked-regions">
                    <div class="tags-container">
                        <el-tag v-for="province in currentChannel.regionBlacklist.provinces" :key="province" type="danger" class="province-tag">{{ getProvinceLabel(province) }}</el-tag>
                        <el-tag v-for="city in currentChannel.regionBlacklist.cities" :key="city" type="warning" class="city-tag">{{ getCityLabel(city, findProvinceByCity(city)) }}</el-tag>
                    </div>
                  </div>
                  <div class="region-rules">
                    <div v-for="(rule, index) in regionRules" :key="index" class="region-rule-row">
                        <el-select v-model="rule.province" @change="(val) => handleProvinceChange(val, index)" filterable placeholder="选择省份" class="province-select">
                          <el-option v-for="province in availableProvinces" :key="province.value" :label="province.label" :value="province.value"></el-option>
                      </el-select>
                        <el-select v-model="rule.cities" multiple filterable placeholder="选择城市" class="city-select" :disabled="!rule.province" @change="handleCitiesChange(rule.province, $event)">
