<template>
  <div class="global-notifications">
    <transition-group name="notification">
      <div 
        v-for="notification in notifications" 
        :key="notification.id"
        class="notification-item"
        @click="handleNotificationClick(notification)"
      >
        <div class="notification-content">
          <i class="fas fa-comment"></i>
          <div class="notification-text">
            <div class="notification-title">新消息提醒</div>
            <div class="notification-message">
              订单 {{ notification.displayId }}: {{ notification.message }}
            </div>
          </div>
          <button 
            class="close-btn"
            @click.stop="removeNotification(notification.id)"
          >
            <i class="fas fa-times"></i>
          </button>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'GlobalMessageListener',
  data() {
    return {
      notificationSound: null,
      socketInitialized: false,
      processedMessageIds: new Set(),
      notifications: [],
      activeChatOrders: new Set()
    }
  },
  methods: {
    initSocket() {
      if (this.socketInitialized) return;

      if (!this.$socket) {
        console.error('Socket not initialized');
        return;
      }

      const userId = localStorage.getItem('currentUserId');
      const userRole = localStorage.getItem('userRole');
      
      // 加入用户特定房间
      this.$socket.emit('join-chat', { 
        userId: userId
      });
      
      // 移除旧的监听器
      this.$socket.off('merchantMessage');
      
      // 监听所有商家消息
      this.$socket.on('merchantMessage', (data) => {
        
        // 检查消息是否已经处理过
        if (this.processedMessageIds.has(data.message._id)) {
          return;
        }
        this.processedMessageIds.add(data.message._id);
        
        // 检查是否是当前用户的订单或超级管理员
        const isOrderOwner = data.orderUserId === userId;
        const isSuperAdmin = userRole === 'superadmin';
        
        // 如果是超级管理员或订单所有者
        if (isSuperAdmin || isOrderOwner) {
          // 检查该订单的聊天窗口是否打开
          const shouldShowNotification = !this.activeChatOrders.has(data.orderId);
          
          // 播放提示音
          this.playNotificationSound();
          
          // 如果聊天窗口没有打开，显示通知
          if (shouldShowNotification) {
            // 创建通知对象
            const notification = {
              id: Date.now() + Math.random(),
              orderId: data.orderId,
              displayId: data.orderId.slice(-8),
              message: data.message.content,
              timestamp: new Date(),
              removing: false
            };

            // 添加到通知列表
            this.notifications.unshift(notification);

            // 限制最多显示 5 条通知
            if (this.notifications.length > 5) {
              const oldNotifications = this.notifications.slice(5);
              this.notifications = this.notifications.slice(0, 5);
              
              // 标记要移除的通知
              oldNotifications.forEach(n => {
                n.removing = true;
              });
            }

            // 5秒后自动移除通知
            setTimeout(() => {
              this.removeNotification(notification.id);
            }, 5000);
          }

          // 触发全局事件
          this.$root.$emit('new-chat-message', {
            orderId: data.orderId,
            message: data.message,
            isOwner: isOrderOwner
          });
        }
      });

      this.socketInitialized = true;
    },

    playNotificationSound() {
      if (this.notificationSound) {
        this.notificationSound.currentTime = 0;
        this.notificationSound.play().catch(error => {
          console.warn('Failed to play notification sound:', error);
        });
      }
    },

    addNotification(notification) {
      // 生成唯一ID
      const notificationId = Date.now() + Math.random();
      
      // 添加新通知到数组开头
      this.notifications.unshift({
        id: notificationId,
        orderId: notification.orderId,
        displayId: notification.displayId || notification.orderId.slice(-8),
        message: notification.message,
        timestamp: new Date()
      });

      // 限制最多显示 5 条通知
      if (this.notifications.length > 5) {
        this.notifications = this.notifications.slice(0, 5);
      }

      // 5秒后自动移除通知
      setTimeout(() => {
        this.removeNotification(notificationId);
      }, 5000);
    },

    removeNotification(id) {
      const index = this.notifications.findIndex(n => n.id === id);
      if (index !== -1) {
        // 标记通知为正在移除
        this.$set(this.notifications[index], 'removing', true);
        
        // 等待动画完成后再移除
        setTimeout(() => {
          this.notifications = this.notifications.filter(n => n.id !== id);
        }, 300);
      }
    },

    handleNotificationClick(notification) {
      if (!notification || !notification.orderId) return;
      
      // 跳转到订单查询页面
      this.$router.push({
        path: '/order-query',
        query: { orderId: notification.orderId }
      });

      // 移除该通知
      this.removeNotification(notification.id);
    },

    handleChatWindowState(orderId, isOpen) {
      if (isOpen) {
        this.activeChatOrders.add(orderId);
      } else {
        this.activeChatOrders.delete(orderId);
      }
    }
  },
  mounted() {
    // 确保通知数组被正确初始化
    this.notifications = [];
    
    // 只有登录用户才初始化
    if (localStorage.getItem('isLoggedIn')) {
      this.initSocket();
      
      // 初始化通知声音
      this.notificationSound = new Audio('/jhm/notification.mp3');
      this.notificationSound.load();
      
      // 请求通知权限
      if ("Notification" in window && Notification.permission === "default") {
        Notification.requestPermission();
      }

      // 监听聊天窗口状态变化
      this.$root.$on('chat-window-state', this.handleChatWindowState);
    }
  },
  beforeDestroy() {
    if (this.$socket) {
      const userId = localStorage.getItem('currentUserId');
      this.$socket.emit('leave-chat', {
        userId: userId
      });
      this.$socket.off('merchantMessage');
      this.socketInitialized = false;
    }

    // 移除事件监听
    this.$root.$off('chat-window-state', this.handleChatWindowState);
  }
}
</script>

<style scoped>
