  overflow: hidden;
  pointer-events: auto;
  cursor: pointer;
  transition: transform 0.2s;
  opacity: 1;
  transform: translateX(0);
}

.notification-item:hover {
  transform: translateY(-2px);
}

.notification-content {
  padding: 15px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.notification-content i {
  color: #6366F1;
  font-size: 20px;
}

.notification-text {
  flex: 1;
}

.notification-title {
  font-weight: 600;
  color: #1F2937;
  margin-bottom: 4px;
}

.notification-message {
  color: #6B7280;
  font-size: 14px;
}

.close-btn {
  background: none;
  border: none;
  color: #9CA3AF;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
}

.close-btn:hover {
  background: #F3F4F6;
  color: #4B5563;
}

/* 确保通知动画正确工作 */
.notification-enter-active,
.notification-leave-active {
  transition: all 0.3s ease;
}

.notification-enter,
.notification-leave-to {
  opacity: 0;
  transform: translateX(100%);
}

